




















import Vue from 'vue'
import Todo from '../views/Todo.vue'
import { Component,Prop} from 'vue-property-decorator'

enum TODO_STATUS{
     pending,
     completed
}

interface TodoObject {
    id: number;
    name: string;
    status: Boolean;
}

@Component({components: {
    Todo
    },
})
export default class TodoList extends Vue{

    todolist:TodoObject[] = [];
    count:number = 0;
    new_todo:string = "";


    getTodoIndex(id: Number){
        this.todolist.map((ele:TodoObject,index:number)=>{
            if(ele.id === id ){
                return index;
            }
        })
        return null;
    }

    createTodo(name:string){
    
        this.count += 1;
        this.todolist.push({
            id: this.count,
            name: this.new_todo,
            status: false,
        });
        this.new_todo = "";
        
    }

    deleteTodo(id: number){
        console.log('Delete TODO');
        console.log(id);
        this.todolist.splice(id-1,1); 
    }

    updateTodo(id: number){
        console.log(id);

    }

}
